@import "variable.scss";

/* メインパーツにid or class をindexをボディにつけ、その中に入れ子にしてCSSを記述する */
/* プラウザのキャッシュクリア　ショートカット　Command ＋shift + R */

/* フォントファミリー　あとで決める */

/* Scss記述に修正する。 */


a {
  text-decoration: none;
}

.logo {
  margin: 15px 10px 0;
  padding-left: 10px;
  letter-spacing: 0.1;
  font-size: 1.4rem;
}

.logo-g {
  font-size: 1.8rem;
}

.logo a {
  color: rgb(99, 90, 90);
}

.header-content {
  display: flex;
  justify-content: space-between;
  background-color: rgb(202, 199, 195, 0.4);

  .header-list {
    display: flex;
    justify-content: space-between;
    padding: 15px 10px 10px 40px;


  }
}
.header-list li {
  margin: 0 0 0 20px;
  padding-top: 5px;
  font-size: 0.7rem;
  span {
    font-weight: normal;
    font-size: 0.1em;
    margin:0 auto;
  }
}

.header-list li a {
  text-decoration: none;
  color: grey;
  font-weight: bolder;
}

.header-list li a:hover {
  color: rgb(24, 23, 23);
}

#header {
  widows: 100%;
  h1 {
    color: blue;
  }
}

@media (max-width: 380px) {
  .header-content {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;

    .logo {
      font-size: 1rem;
    }


  }
}

@media (max-width: 320px) {
  /* レスポンシブ　320pxだと、headerバランス悪い 位置考える いっそのことハンバーガーにするか。*/

  .header-list {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-left: 100px;
    margin-top: -0.3px;
  }

  .logo {
    font-size: 1rem;
  }
}

@media (min-width: 768px) {
  .header-content {
    padding: 5px 50px;
    .logo {
      margin: 5px 10px 0;
      padding-left: 15px;
      letter-spacing: 0.1;
      font-size: 1.7rem;

      .logo-g{
        font-size:2.3rem;
      }
    }
  }



  .header-list li {
    font-size: 1.1em;
    margin-right: 20px;
    line-height: 1;

    span {
      font-size: 0.8rem;
      line-height: 0.5;
      margin-left: 5px;

    }
  }
}
